.top-section-main-pc {
    width: 96%;
    height: 100vh;
    background-color: #2D3080;
    border-radius: 2vw;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;

    .left-section {
        position: relative;
        width: 50%;
        height: 100%;
        // border: 1px solid #000;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        overflow: hidden;
        padding-left: 4%;
        gap: 2em;

        .blured-circle3 {
            position: absolute;
            width: 100%;
            top: -40%;
            left: -40%;
            object-fit: contain;
        }



        h3 {
            font-size: 3vw;
            color: #fff;
            line-height: 1.1;
            font-weight: 500;

            span {
                color: #FFC537;
            }

            .hr-span {
                width: 100%;
                height: 2px;
                // background-color: #fff;
                margin-top: 2%;
                background: linear-gradient(90deg, rgba(255, 255, 255, 1) 40%, rgba(255, 255, 255, 0) 100%);
                border-radius: 10px;
            }

        }

        p {
            position: relative;
            font-size: 1.2vw;
            color: #fff;

            .img-span {
                position: absolute;
                top: -0.8em;
                left: -1em;
                width: 1.4em;
                aspect-ratio: 1;
                // background-color: #fff;
                background-size: contain;
                background-repeat: no-repeat;
                background-position: center;

            }


        }


        .input-field-main {
            position: relative;
            width: 60%;
            height: 4vw;
            background-color: #fff;
            border-radius: 500px;
            display: flex;
            justify-content: center;
            align-items: center;
            overflow: hidden;
            padding-left: 1em;

            .input-field {
                width: 100%;
                height: 100%;
                font-size: 1vw;
            }

            button {
                position: absolute;
                height: 3.2vw;
                right: 0.5em;
                z-index: 1;
                padding: 0 1em;
                background-color: #FFB500;
                border-radius: 500px;
                font-size: 1vw;
                color: #000;
                font-weight: 600;
                box-shadow: 0.2em 0.2em 2px #0000007c;
            }


        }
    }

    .right-section {
        position: relative;
        width: 50%;
        height: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: center;

        .avatar-img {
            position: relative;
            width: 80%;
            object-fit: contain;
            z-index: 1;
        }

        .blured-circle1,
        .blured-circle2 {
            position: absolute;
            z-index: 0;
            min-width: 150%;
            object-fit: contain;
        }

        .blured-circle1 {
            left: -10%;

        }

        .blured-circle2 {
            top: 20%;
            left: -30%;

        }
    }

}

