.german-main-mob{
    position: relative;
    width: 100%;
    height:45vh;
    background: rgb(45,48,128);
background: linear-gradient(180deg, rgba(45,48,128,1) 0%, rgba(9,10,26,1) 100%);
overflow: hidden;

.bg1-img-shash-mb{
    position: absolute;
    left:0%;
    top:0%;
    width:165%;
    height:100%;
    // height:110%;
    object-fit: cover;
    // border:1px solid green;
}
.cd-text-cont-mb{
    position: absolute;
    top:45%;
    left:10%;
    transform:translateY(-50%);

.cd-text-h2-mb{
color:#FFC537;
font-size: 7.5vw;
line-height: 1.1;
letter-spacing: 0.05vw;
font-weight: 500;
}
.cd-text-p-mb{
    color:white;
    font-size: 4.5vw;
    font-weight: 400;
    margin-top: 1vw;
    }
}
.review-box-shash-mb{
    position: absolute;
    top:65%;
    left:10%;
    width: 55%;
    height:13.5vh;
    background:black;
    border-radius:1vw;
    padding: 0.5vw 1.5vw;
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;

.rbs-div1-mb{
    width: 100%;
    display:flex;
    justify-content: space-between;
    // border:1px solid red;

    .rbs1-mb,.rbs2-mb{
        width: 50%;
        display:flex;
        flex-direction: column;
        color:white;
        // border:1px solid red;
        p{
            color:#979797;
            font-size: 3.05vw;
            font-weight: 100;
        }
        h3{
            font-size: 3.9vw;
            font-weight: 300;
        }
    }
    .rbs1-mb{
        padding-left: 2.5%;
        // border-left: 1px solid white;
    }
    .rbs2-mb{
        padding-left: 12.5%;
        border-left: 1px solid white;
    }
}
.rbs-div2-mb{
    width: 100%;
    display:flex;
    justify-content: space-between;
    .rbs3-mb{
        width: 50%;
        display:flex;
        flex-direction: column;
        color:white;
        padding-left: 2.5%;

        &:nth-child(2){
            padding-left: 12.5%;
            border-left: 1px solid white;
        }
        p{
            color:#979797;
            font-size: 3.05vw;
            font-weight: 100;
        }
        h3{
            font-size: 3.9vw;
            font-weight: 300;
        }
    }
 
}
}
}