.nursing-in-germany-main {
    position: relative;
    width: 100vw;
    height: 100vh;
    // background-color: #2D3080;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;


    .upper-section {
        width: 100%;
        height: 35%;
        // border: 1px solid #000;
        display: flex;
        justify-content: center;
        align-items: center;

        h3 {
            font-size: 2.8vw;
            font-weight: 500;
            text-align: center;
            line-height: 1.2;
            color: #000;
            transition: all 0.5s ease-in-out;

            span {
                color: #373BB4;
            }
        }
    }

    .lower-section {
        width: 100%;
        height: 65%;
        // border: 1px solid #000;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        filter: drop-shadow(0.6vw 0.8vw 2px #0000001d);

        .ng-card {
            position: absolute;
            // background-color: #fff000;
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: all 0.4s ease-in-out;
            transform: translateY(110%) scale(1);

            .card-img {
                width: 60%;
                object-fit: contain;
                // box-shadow: 0.2vw 0.2vw 2px #000 ;

            }
        }

    }

}

@media (width < 680px) {
    .nursing-in-germany-main-mobile {
        position: relative;
        width: 100vw;
        height: 100vh;
        // background-color: #2D3080;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        gap: 8%;


        .upper-section {
            width: 100%;
            height: 25%;
            // border: 1px solid #000;
            display: flex;
            justify-content: center;
            align-items: center;

            h3 {
                font-size: 5vw;
                font-weight: 500;
                text-align: center;
                line-height: 1.2;
                color: #000;
                transition: all 0.5s ease-in-out;

                span {
                    color: #373BB4;
                }
            }
        }

        .lower-section {
            width: 100%;
            height: 65%;
            // border: 1px solid #000;
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            // filter: drop-shadow(0.6vw 0.8vw 2px #0000001d);

            .ng-card {
                position: absolute;
                // background-color: #fff000;
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                transition: all 0.4s ease-in-out;
                transform: translateY(120%) scale(1);

                .card-img {
                    width: 60%;
                    object-fit: contain;
                    // box-shadow: 0.2vw 0.2vw 2px #000 ;

                }
            }

        }

    }
}