.other-section {
    position: relative;
    width: 100%;
    height: 100vh;
    // border:1px solid red;
    overflow: hidden;

    .scards-main {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        // border: 1px solid green;
        overflow: hidden;

        .linee-bg {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            // width: 100%;
            height: 100%;
            // object-fit: contain;
        }

        .div-sec1 {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 0vh;
            background-color: #5358DB;
            border-bottom-left-radius: 3.5vw;
            border-bottom-right-radius: 3.5vw;
            overflow: hidden;
            filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.75));

            .div-sec2 {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 37.5vh;
                background: rgb(255, 255, 255);
                background: linear-gradient(90deg, rgba(255, 255, 255, 0.1) 0%, rgba(207, 207, 207, 0.1) 97%);
                border-bottom-left-radius: 3.5vw;
                border-bottom-right-radius: 3.5vw;
                overflow: hidden;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));


                .sha-bg {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    object-fit: contain;

                }

                .ds2-h1 {
                    text-align: center;
                    font-size: 3.25vw;
                    transform: translateY(200%);
                    line-height: 1.2;
                }

                .ds2-p {
                    font-size: 1.2vw;
                    transform: translateY(500%);
                }
            }
        }

        .cards-section {
            position: absolute;
            bottom: 5%;
            left: 5%;
            min-width: 90%;
            display: flex;
            justify-content: space-evenly;
            transform: translateX(90vw);
            z-index: 1;
        }

        .ellip-violet {
            position: absolute;
            width: 100%;
            bottom: -10%;
        }
    }
}