.detailContent{
    width: 100%;
    height: 100%;
    .dcHeading{
        font-size: 22px;
        font-weight: 500;
        margin: 20px 0px 30px 0px;
        position: relative;
        width: max-content;
        &::after {
            content: "";
            width: 30%;
            height: 10px;
            background-color: #ffc537;
            position: absolute;
            right:1%;
            top: 50%;
            z-index: -1;
            clip-path: polygon(0 0, 83% 0, 100% 100%, 12% 100%);
          }

    }
}