.why-germany-main {
    width: 100%;
    height: 100vh;
    // background-color: #2D3080;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;

    .top-section {
        position: relative;
        width: 100%;
        height: 40%;
        // border: 1px solid #000;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-start;
        padding-left: 4%;

        h3 {
            width: 30%;
            font-size: 3vw;
            color: #000;
            font-weight: 500;
            display: flex;
            // border: 1px solid #000;
            gap: 5%;

            .img-span {
                width: 3.5vw;
                aspect-ratio: 1;
                // background-color: #fff;
                background-size: contain;
                background-repeat: no-repeat;
                background-position: center;
            }
        }

        p {
            font-size: 1.4vw;
            color: #000;
        }

        button {
            position: absolute;
            height: 3.2vw;
            top: 43%;
            right: 4%;
            z-index: 1;
            padding: 0 1.4em;
            background-color: #FFB500;
            border-radius: 500px;
            font-size: 1vw;
            color: #000;
            font-weight: 600;
            box-shadow: 0.2em 0.2em 2px #0000007c;
        }

    }

    .bottom-section {
        position: relative;
        width: 100%;
        height: 60%;
        background: linear-gradient(180deg, rgba(145, 148, 255, 1) 0%, rgba(39, 43, 153, 1) 180%);
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;

        .ellipse {
            position: absolute;
            top: -3vw;
            left: 0vw;
            width: 100vw;
            height: 6vw;
            z-index: 1;
            background-color: #fff;
            border-radius: 100%;
            // display: none;
        }

        .card-slider {
            position: relative;
            width: 100%;
            height: 100%;
            // background-color: #000;
            display: flex;
            justify-content: center;
            align-items: center;
            // overflow: hidden;
            scroll-snap-align: center;

            .slides {
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                gap: 2%;
                // overflow-x: auto;
                // overflow-y: hidden;
                scroll-snap-type: x mandatory;
                transition: all 0.2s ease-out;

            }

        }
    }

}

