// =================== WG CARD ========================

.wg-card-main {
    position: relative;
    min-width: 22vw;
    height: 110%;
    // background-color: #2B2E7C;
    border-radius: 3.5vw;
    box-shadow: 0.4em 0.2em 4px #00000047;
    display: flex;
    justify-content: center;
    align-items: center;
    scroll-snap-align: center;

    // -------------------------------

    .heading-section {
        position: absolute;
        top: 34%;
        width: 100%;
        height: 7vw;
        // border: 1px solid #000;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding-left: 4%;

        .icon-img {
            position: absolute;
            width: 12%;
            top: -40%;
            left: 4%;
            user-select: none;
            transition: all 0.1s ease-out;
            // border: 4px solid #000;

        }

        .heading-text {
            // color: #fff;
            width: 60%;
            line-height: 1.2;
            transition: all 0.1s ease-out;
            // border: 1px solid #000;


            .text-span {
                font-size: 1.1vw;
                font-weight: 500;
                user-select: none;

            }

            .text-span2 {
                font-size: 2vw;
                font-weight: 600;
                border-bottom: 1px solid;
                user-select: none;

            }
        }

        // ----------CARD NUMB------------------

        .card-no {
            position: relative;
            width: 35%;
            height: 70%;
            // background-color: #fff;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            // border: 1px solid #000;

            .span1 {
                position: absolute;
                font-size: 5.5vw;
                font-weight: 900;
                user-select: none;
                transition: all 0.1s ease-out;


                // fill-color: #00000010;
                // stroke-width: 1px;
                // stroke-color: #ffffff05;
                // -webkit-text-fill-color: #00000010;
                // -webkit-text-stroke-width: 1px;
                // -webkit-text-stroke-color: #ffffff05;
            }

        }
    }

    // -------------------------------

    h3 {
        position: absolute;
        bottom: 25%;
        font-size: 5vw;
        font-weight: 600;
        // color: #ffffff12;
        user-select: none;
        text-transform: uppercase;
    }

    // -------------------------------
    .frame-sec {
        position: absolute;
        left: 4%;
        bottom: 10%;
        width: 92%;
        height: 8.8vw;
        // background-color: #fff;
        border-radius: 0.6vw;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 2%;

        .inner-frame {
            width: 100%;
            height: 100%;
            border-radius: 0.5vw;
            overflow: hidden;

            .frame-img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                user-select: none;
                transition: all 0.1s ease-out;

            }

        }
    }

}

