.query-page-main-mb{
    position: relative;
    width: 100%;
    height: 32.5vh;
    // border:1px solid red;
    display:flex;
    justify-content: center;
    align-items: center;

.qp-cont-outside-mb{
    width: 80%;
    height: 60%;
    border-radius: 2.75vw;
    display:flex;
    justify-content: center;
    align-items: flex-end;
    background: rgb(255,197,55);
background: linear-gradient(173deg, rgba(255,197,55,1) 0%, rgba(255,197,55,0) 100%);
    .qp-cont-mb{
        width: 99.2%;
        height:99.5%;
        background-color: #FFE8AF;
        // border:1px solid red;
        border-radius: 2.75vw;
        display:flex;

        .qp-inside1-mb{
            position: relative;
         width: 100%;
         height:100%;
         display:flex;
         flex-direction: column;
         justify-content: space-evenly;
         align-items: center;
         padding:1.5vw;
         padding-bottom: 1vw;
        //  border:1px solid yellow;

         .qp-p-mb{
            font-size: 4.2vw;
            color: #1e1e1e;
            letter-spacing: 0.1vw;
         }
         .qp-h2-mb{
            font-size: 5.65vw;
            line-height: 1;
            font-weight: 500;
         }

         .input-btn-div-mb{
            width: 80%;
            padding:1.5vw;
            background-color: white;
            border-radius: 5vw;
            display: flex;
            justify-content: space-between;
            align-items: center;
            // overflow: hidden;
            .input-shash-mb{
                width: 65%;
                // border:none;
                outline:none;
                font-size: 3.1vw;
                // border:1px solid green;
                &::placeholder {
                    font-size: 2.75vw;
                    color: #C5C5C5;
                    text-align: center;
                }
            }
            input[type="number"]::-webkit-inner-spin-button,
            input[type="number"]::-webkit-outer-spin-button {
                -webkit-appearance: none;
                margin: 0;
            }
    
            /* Hide the arrows in Firefox */
            input[type="number"] {
                -moz-appearance: textfield;
            }
    
            /* Hide the arrows in Edge and IE */
            input[type="number"] {
                appearance: textfield;
            }

            .input-btn-mb{
                background-color: #FFC537;
                padding:1.5vw;
                border-radius: 3.5vw;
                font-size: 2.8vw;
                font-weight: 500;
            }
         }
        }
    }
}
}