        section.bottom-section {
            width: 100%;
            height: 55%;
            // border: 1px solid #000;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            overflow: hidden;
            gap: 10%;

            .bottom-heading {
                width: 75%;
                object-fit: contain;
            }

            p {
                padding: 0 10%;
                font-size: 4vw;
                text-align: center;
            }
        }
    }

}