.opportunities-main {
    width: 100%;
    height: 100vh;
    // background-color: #2D3080;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;

    .top-section {
        position: relative;
        width: 100%;
        height: 35%;
        // border: 1px solid #000;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        padding-left: 4%;

        h3 {
            width: 60%;
            font-size: 3vw;
            color: #000;
            font-weight: 500;
            display: flex;
            // border: 1px solid #000;
            gap: 2%;

            .img-span {
                width: 4.5vw;
                aspect-ratio: 1;
                // background-color: #fff;
                background-size: contain;
                background-repeat: no-repeat;
                background-position: center;
                margin-top: -2vw;
            }
        }

        p {
            font-size: 1.4vw;
            color: #000;
        }

    }

    .bottom-section {
        position: relative;
        width: 100%;
        height: 65%;
        // background: linear-gradient(180deg, rgba(145, 148, 255, 1) 0%, rgba(39, 43, 153, 1) 180%);
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        // padding: 0 4%;
        // border: 1px solid #000;

        .ellipse1,
        .ellipse2 {
            position: absolute;
            // left: 0vw;
            width: 105vw;
            height: 8vw;
            z-index: 1;
            background-color: #fff;
            background: linear-gradient(90deg, rgba(45, 48, 128, 1) 0%, rgba(232, 233, 255, 1) 100%);
            border-radius: 100%;
            // display: none;
            display: flex;
            justify-content: center;
            align-content: center;
            padding: 0.4vw;

            .inner-ellipse {
                width: 100%;
                height: 100%;
                border-radius: 100%;
                background-color: #fff;
            }
        }


        .ellipse1 {
            top: -5vw;

        }

        .ellipse2 {
            bottom: -5vw;

        }


        .card-slider {
            position: relative;
            width: 100%;
            height: 100%;
            // background-color: #000;
            display: flex;
            justify-content: center;
            align-items: center;
            overflow: hidden;
            scroll-snap-align: center;

            .slides {
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                gap: 2%;
                // overflow-x: auto;
                // overflow-y: hidden;
                scroll-snap-type: x mandatory;
                transition: all 0.2s ease-out;


            }

        }
    }

}

