.co-card-main {
    position: relative;
    min-width: 20vw;
    height: 100%;
    // background-color: #2B2E7C;
    border-radius: 2vw;
    box-shadow: 0.4em 0.2em 4px #00000047;
    display: flex;
    justify-content: center;
    align-items: center;
    scroll-snap-align: center;

    // -------------------------------

    .heading-section {
        position: absolute;
        top: 24%;
        width: 100%;
        height: 7vw;
        // border: 1px solid #000;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding-left: 4%;

        .icon-img {
            position: absolute;
            width: 14%;
            top: -45%;
            left: 3%;
            user-select: none;
            transition: all 0.1s ease-out;
            // border: 4px solid #000;

        }

        .icon-img1 {
            position: absolute;
            width: 12%;
            top: -44%;
            left: 24%;
            user-select: none;
            transition: all 0.1s ease-out;
            // border: 4px solid #000;

        }

        .icon-img2 {
            position: absolute;
            width: 6%;
            top: -30%;
            left: 17%;
            user-select: none;
            transition: all 0.1s ease-out;
            // border: 4px solid #000;

        }

        .heading-text {
            // color: #fff;
            width: 60%;
            line-height: 1.2;
            transition: all 0.1s ease-out;
            // border: 1px solid #000;

            .text-span {
                font-size: 1.8vw;
                font-weight: 600;
                border-bottom: 1px solid;
                user-select: none;

            }
        }

        // ----------CARD NUMB------------------

        .card-no {
            position: relative;
            width: 35%;
            height: 50%;
            // background-color: #fff;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            // border: 1px solid #000;

            .span1 {
                position: absolute;
                font-size: 5vw;
                font-weight: 900;
                user-select: none;
                transition: all 0.1s ease-out;


                // fill-color: #00000010;
                // stroke-width: 1px;
                // stroke-color: #ffffff05;
                // -webkit-text-fill-color: #00000010;
                // -webkit-text-stroke-width: 1px;
                // -webkit-text-stroke-color: #ffffff05;
            }

        }
    }

    // -------------------------------
    .frame-sec {
        position: absolute;
        left: 2%;
        // bottom: 10%;
        top: 50%;
        width: 96%;
        height: 8.8vw;
        // background-color: #fff;
        border-radius: 0.6vw;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 2%;

        .inner-frame {
            width: 100%;
            height: 100%;
            border-radius: 0.5vw;
            overflow: hidden;
            // background-color: #fff;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;

            .details {
                width: 100%;
                // height: 100%;
                display: flex;
                justify-content: center;
                align-items: flex-start;
                gap: 4%;
                padding: 0 3%;
                // background-color: #fff000;

                .icon {
                    width: 4%;
                    object-fit: contain;
                    margin-top: 3.2%;
                }

                p {
                    width: 90%;
                    font-size: 1.25vw;
                    font-weight: 500;
                    transform-origin: left;
                }
            }

        }
    }

    .many-more-span {
        position: absolute;
        top: calc(50% + 9.8vw);
        font-size: 1.1vw;

    }

}


