.query-page-main{
    position: relative;
    width: 100%;
    height: 34.5vh;

.qp-cont-outside{
    position: absolute;
    bottom:0;
    left:50%;
    transform: translateX(-50%);
    width: 50vw;
    height: 80%;
    border-radius: 0.35vw;
    display:flex;
    justify-content: center;
    align-items: flex-end;
    background: rgb(255,197,55);
background: linear-gradient(173deg, rgba(255,197,55,1) 0%, rgba(255,197,55,0) 100%);
    .qp-cont{
        width: 99.5%;
        height:99.25%;
        background-color: #FFE8AF;
        // border:1px solid red;
        border-radius: 0.35vw;
        display:flex;

        .qp-inside1{
            position: relative;
         width: 40%;
         height:100%;
         display:flex;
         flex-direction: column;
         justify-content: space-evenly;
         padding:1.5vw;
         padding-bottom: 1vw;
        //  border:1px solid yellow;

         .qp-p{
            font-size: 1.2vw;
            color: #1e1e1e;
         }
         .qp-h2{
            font-size: 1.65vw;
            line-height: 1;
            font-weight: 500;
         }

         .input-btn-div{
            padding:0.5vw;
            background-color: white;
            border-radius: 2vw;
            display: flex;
            justify-content: space-between;
            align-items: center;
            // overflow: hidden;
            .input-shash{
                width: 65%;
                // border:none;
                outline:none;
                font-size: 1.1vw;
                // border:1px solid green;
                &::placeholder {
                    font-size: 0.75vw;
                    color: #C5C5C5;
                    text-align: center;
                }
            }
            input[type="number"]::-webkit-inner-spin-button,
            input[type="number"]::-webkit-outer-spin-button {
                -webkit-appearance: none;
                margin: 0;
            }
    
            /* Hide the arrows in Firefox */
            input[type="number"] {
                -moz-appearance: textfield;
            }
    
            /* Hide the arrows in Edge and IE */
            input[type="number"] {
                appearance: textfield;
            }

            .input-btn{
                background-color: #FFC537;
                padding:0.5vw;
                border-radius: 1.5vw;
                font-size: 0.8vw;
                font-weight: 500;
            }
         }
        }
        .qp-inside2{
            position: relative;
         width:60%;
         height:100%;

         .qp-colored-div{
            position: absolute;
            bottom:0;
            left:20%;
            width: 46%;
            height:85%;
            aspect-ratio: 1 / 1;
            background: rgb(239,170,0);
            background: linear-gradient(172deg, rgba(239,170,0,1) 0%, rgba(168,119,0,1) 100%);
            border-top-left-radius: 0.5vw;
            border-top-right-radius: 0.5vw;

            .qp2-p{
                position: absolute;
                top:10%;
                left:4%;
                padding:0.5vw;
                background-color: white;
                color:black;
                font-size: 0.85vw;
                font-weight: 500;
                border-radius: 0.75vw;
                border-top-left-radius: 0;
            }
            .text-imgg-div{
                 position: absolute;
                 top:40%;
                 left:30%;
                 display: flex;
                 min-width:150% ;
                .reply-text{
                    background: rgb(17,17,17);
background: linear-gradient(141deg, rgba(17,17,17,1) 0%, rgba(119,119,119,1) 100%);
color:white;
font-size: 0.85vw;
font-weight: 500;
border-radius: 1.2vw;
border-bottom-right-radius: 0;
padding:0.75vw;
                }
                .reply-img{
                    position: relative;
                    top:50%;
                    width: 10%;
                    object-fit: contain;
                    align-self: flex-end;
                }
            }
         }

        }
    }
}
}